import React, { Component } from 'react';

class Auth extends Component {

    state = {
        ok: false
    }

    componentDidMount() {
        setTimeout(() => {
            this.setState({ ok: !this.state.ok })
        }, 3000)
    }

    componentDidUpdate() {
        if (this.state.ok) {
            console.log("Redirect to Home")
        } else {
            console.log("Redirect to Login")
        }
    }
    render() {
        return (
            <React.Fragment>
                {this.props.children}
            </React.Fragment>
        );
    }
}

export default Auth;
